                position: "absolute",
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                background: `radial-gradient(circle at 50% 0%, ${climateColor}05 0%, transparent 50%)`,
                borderRadius: "16px",
                opacity: 0,
                transition: "opacity 0.4s ease",
                pointerEvents: "none",
